:root {
  --bg: #000;
  --card: #1f1f1f;
  --card-alt: #262626;
  --text: #fff;
  --muted: #b5b5b5;
  --accent: #178a2f;
  --accent-lock: #0f5a1f;
  --danger: #c62828;
  --banner: #222;
  --border: #333;
  --focus: #ff2d2d;
  --logo-url: url("https://blapoker.com/wp-content/uploads/logo_rev.png");
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", Arial, sans-serif;
}

html, body, #root {
  height: 100%;
  margin: 0;
  background: var(--bg);
  color: var(--text);
  -webkit-font-smoothing: antialiased;
}

body {
  background:
    radial-gradient(circle at center, rgba(0,0,0,0.3) 0%, var(--bg) 70%),
    var(--logo-url) center 140px / 580px auto no-repeat,
    var(--bg);
}

.app-container {
  min-height: 100%;
  display: flex;
  justify-content: center;
  padding: 30px 16px 80px;
  box-sizing: border-box;
}

.content-wrapper {
  width: 100%;
  max-width: 640px;
  position: relative;
  z-index: 2;
}

h1 {
  margin: 0 0 8px;
  font-size: 44px;
  font-weight: 700;
  letter-spacing: 1px;
  text-align: center;
}

h2 {
  margin: 0 0 16px;
  font-size: 28px;
  font-weight: 600;
}

p {
  margin: 4px 0;
  line-height: 1.4;
}

.logged-in {
  text-align: center;
  margin-top: 6px;
  margin-bottom: 4px;
  font-size: 15px;
}

.leader-banner {
  margin: 14px auto 26px;
  background: var(--banner);
  border: 1px solid var(--border);
  color: #f3e3bb;
  padding: 10px 18px;
  border-radius: 10px;
  font-size: 15px;
  max-width: 540px;
  text-align: center;
}

.leader-banner.internal {
  margin-top: 0;
  margin-bottom: 24px;
}

.card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 14px;
  padding: 26px 34px 30px;
  margin: 34px auto 0;
  box-sizing: border-box;
  max-width: 560px;
  width: 100%;
  box-shadow: 0 4px 14px -4px rgba(0,0,0,0.7);
}

.auth-card {
  margin-top: 24px;
}

.status-card h2,
.admin-card h2 {
  font-size: 30px;
  margin-top: 0;
}

.field-group input {
  width: 100%;
  padding: 12px 14px;
  border-radius: 8px;
  border: 1px solid #444;
  background: #101010;
  color: var(--text);
  margin-bottom: 10px;
  font-size: 15px;
}

.field-group input:focus {
  outline: 2px solid var(--focus);
  outline-offset: 0;
}

.field-group button {
  width: 100%;
  padding: 14px 0;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  background: var(--danger);
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  letter-spacing: .5px;
  transition: background .18s;
}

.field-group button:hover:not(:disabled) {
  background: #d43838;
}

.field-group button:disabled {
  opacity: .6;
  cursor: default;
}

.toggle-auth {
  margin-top: 12px;
  font-size: 14px;
  color: var(--muted);
  cursor: pointer;
  text-decoration: underline;
}

.toggle-auth:hover {
  color: #eee;
}

.logout {
  background: var(--danger);
  color: #fff;
  border: none;
  padding: 12px 26px;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  font-weight: 600;
  display: block;
  margin: 10px auto 14px;
  transition: background .2s;
}

.logout:hover {
  background: #d53a3a;
}

.primary {
  background: var(--accent);
  color: #fff;
  border: none;
  padding: 14px 24px;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: .5px;
  cursor: pointer;
  margin: 14px auto 10px;
  display: block;
  min-width: 260px;
  transition: background .2s, transform .15s;
}

.primary:hover {
  background: #199d35;
}

.primary:active {
  transform: translateY(1px);
}

.primary.locked {
  background: #1e4f29;
  position: relative;
}

.primary.locked::after {
  content: "ðŸ”’";
  margin-left: 8px;
  font-size: 16px;
  opacity: .85;
}

.hint {
  font-size: 14px;
  margin-top: 6px;
  color: var(--muted);
  text-align: center;
}

.coupon-list {
  list-style: none;
  padding: 0;
  margin: 20px 0 0;
  font-size: 15px;
}

.coupon-list li {
  margin: 6px 0;
  padding: 6px 10px;
  background: #141414;
  border: 1px solid #2c2c2c;
  border-radius: 8px;
}

.admin-card {
  margin-top: 42px;
}

.admin-users {
  display: flex;
  flex-direction: column;
  gap: 18px;
  margin-top: 4px;
}

.admin-user-row {
  background: var(--card-alt);
  border: 1px solid #333;
  padding: 14px 16px 12px;
  border-radius: 10px;
  font-size: 14px;
}

.admin-user-head {
  font-size: 15px;
  margin-bottom: 6px;
}

.admin-actions button {
  background: var(--accent);
  color: #fff;
  border: none;
  padding: 6px 14px;
  font-size: 14px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  margin-bottom: 8px;
  transition: background .2s;
}

.admin-actions button:hover {
  background: #199d35;
}

.coupon-sublist {
  list-style: none;
  padding: 0;
  margin: 0;
}

.coupon-sublist li {
  margin: 4px 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 6px;
}

.coupon-sublist .redeem {
  background: #2d2d2d;
  border: 1px solid #444;
  color: #fff;
  padding: 4px 10px;
  font-size: 12px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: background .2s, border-color .2s;
}

.coupon-sublist .redeem:hover {
  background: #3b3b3b;
  border-color: #555;
}

/* ---------- Modal / Scanner ---------- */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.72);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 28px 16px 40px;
  z-index: 50;
}

.modal {
  background: #121212;
  border: 1px solid #2e2e2e;
  width: 100%;
  max-width: 640px;
  border-radius: 34px;
  padding: 44px 38px 50px;
  box-shadow: 0 8px 32px -6px rgba(0,0,0,0.85), 0 0 0 1px rgba(255,255,255,0.05) inset;
  position: relative;
  text-align: center;
}

.modal h2 {
  margin: 0 0 18px;
  font-size: 42px;
  font-weight: 700;
  letter-spacing: 1px;
}

.modal-sub {
  margin: 0 0 34px;
  color: #c9c9c9;
  font-size: 16px;
}

.scan-frame {
  width: 100%;
  aspect-ratio: 1 / 1;
  max-width: 580px;
  margin: 0 auto 34px;
  background: #000;
  border-radius: 38px;
  border: 10px solid #111;
  box-shadow:
    0 0 0 3px #262626 inset,
    0 0 0 12px #151515 inset,
    0 0 0 16px #252525 inset,
    0 0 0 22px #b40000 inset,
    0 0 24px -6px rgba(0,0,0,0.9),
    0 6px 40px -8px rgba(0,0,0,0.85);
  position: relative;
  overflow: hidden;
}

.scan-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background: #000;
  border-radius: 24px;
  display: block;
}

.scan-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0,0,0,0.45);
  font-size: 22px;
  font-weight: 500;
  letter-spacing: .4px;
  color: #dedede;
  padding: 10px;
  text-align: center;
}

.modal button {
  border: none;
  font-weight: 600;
  cursor: pointer;
}

.modal .retry {
  background: #383838;
  color: #f5f5f5;
  padding: 14px 44px;
  font-size: 17px;
  border-radius: 18px;
  margin: 0 auto 26px;
  display: inline-block;
  transition: background .25s, transform .2s;
}

.modal .retry:hover {
  background: #454545;
  transform: translateY(-1px);
}

.modal .close {
  background: #454545;
  color: #fff;
  padding: 14px 48px;
  font-size: 19px;
  border-radius: 24px;
  margin: 0 auto;
  display: inline-block;
  transition: background .25s, transform .2s;
}

.modal .close:hover {
  background: #535353;
  transform: translateY(-1px);
}

.scan-hint {
  margin-top: 22px;
  font-size: 14px;
  color: #b8b8b8;
}

@media (max-width: 620px) {
  h1 {
    font-size: 40px;
  }
  .modal {
    padding: 38px 26px 44px;
  }
  .modal h2 {
    font-size: 36px;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 36px;
  }
  .modal h2 {
    font-size: 33px;
  }
  .scan-frame {
    border-width: 8px;
  }
  .primary {
    min-width: 0;
    width: 100%;
  }
}
